@import "../../app.scss";
.active {
  border-bottom: red 2px solid;
}
.item-inactive {
  display: none;
}
.item-active {
  display: block;
}
.navbar-scroll {
  background-color: rgba(255, 255, 255, 0.9);
  position: fixed;
}
.navbar-onTop {
  background-color: transparent;
  position: absolute;
}
.root-navbar {
  display: flex;
  align-items: center;
  width: 100%;
  // justify-content: space-between;
  .hamburger-menu-container {
    display: none;
    @media only screen and (max-width: $sm) {
      display: block;
    }
    button {
      background-color: transparent;
    }
    svg {
      height: 50px;
      width: 50px;
    }
  }
  .logo-container {
    padding: 1rem 3rem;

    img {
      width: 64px;
    }
  }
  nav {
    flex: 1 1 auto;
    @media only screen and (max-width: $sm) {
      display: none;
    }
    ul {
      list-style: none;
      display: flex;
      padding-top: 1em;
      margin-right: 3rem;
      flex-wrap: wrap;
      justify-content: flex-end;
      li {
        margin: 0 1rem;
        a {
          text-decoration: none;
          color: black;
          font-size: 1.25rem;
          font-weight: 600;
          padding-bottom: 2px;
        }
        a:hover {
          border-bottom: red 1px solid;
        }
      }
    }
  }
}

.root-drawer {
  position: fixed;
  left: 0;
  top: 0;
  background-color: rgba(181, 212, 217, 0.85);
  height: 100%;
  width: 250px;
  z-index: 5555;
  button {
    position: relative;
    top: 10px;
    right: -200px;
    background-color: transparent;
    padding: 0;
    svg {
      width: 32px;
      height: 32px;
    }
  }

  div {
    margin: 3rem 1rem;
    ul {
      li {
        margin: 1rem 0;
        a {
          padding: 0.25rem;
          font-size: 1.25rem;
          font-weight: 600;
        }
      }
    }
  }
}
